#root {
  font-family: 'Roboto', sans-serif;
  min-width: 300px;
}

.MuiToolbar-root-36 {
  padding: 16px;
}

.main-content {
  padding: 10px;
  max-width: 900px;
  width: 90%;
  margin: auto;
}
.instructionsContainer {
  /*margin: 10px;*/
}
.instructions {
  color: white;
  text-align: center;
  border: 1px solid white;
  border-radius: 5px;
  padding-top: 20px;
  padding-bottom: 20px;
  margin: 5%;
}

.instructions p {
  margin: 0;
}
.instructions .dismiss {
  text-decoration: underline;
  -webkit-transition: opacity .2s ease;
  -o-transition: opacity .2s ease;
  transition: opacity .2s ease;
}
.instructions .dismiss:hover {
  cursor:pointer;
  opacity: .75;
}



.color-container {
  /*height: 300px;*/
}
.color-container .color-circle {
  width: 100px;
}

.color-circle {
  /*border: 5px solid rgba(0,0,0,.5);*/
  -webkit-transition: -webkit-transform .2s ease;
  transition: -webkit-transform .2s ease;
  -o-transition: transform .2s ease;
  transition: transform .2s ease;
  transition: transform .2s ease, -webkit-transform .2s ease;
  cursor: pointer;
}

.color-circle:hover{
  -webkit-transform: scale(1.03);
      -ms-transform: scale(1.03);
          transform: scale(1.03);
}

.color-circle:active{
  -webkit-transform: scale(.9);
      -ms-transform: scale(.9);
          transform: scale(.9);
}
.fade-enter {
  opacity: 0;
  height: 0%;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
  height: 100%;
}

.fade-leave {
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  /*transform-origin: top;*/
  max-height: 100px;

}

.fade-leave.fade-leave-active {
  opacity: 0;
  -webkit-transition: 500ms;
  -o-transition: 500ms;
  transition: 500ms;
  -webkit-transform: scale(1,0);
      -ms-transform: scale(1,0);
          transform: scale(1,0);
  max-height: 0px;


}
.fadeOut {
  -webkit-animation: fade .25s forwards;
          animation: fade .25s forwards;
}

.fadeIn {
  -webkit-animation: fadeIn .25s;
          animation: fadeIn .25s;
}

@-webkit-keyframes fadeIn {
  0% {opacity: 0}
  100% {opacity: 1}
}

@keyframes fadeIn {
  0% {opacity: 0}
  100% {opacity: 1}
}

@-webkit-keyframes fade {
  0% {
    opacity: 1
  }
  100% {
    opacity: 0
  }
}

@keyframes fade {
  0% {
    opacity: 1
  }
  100% {
    opacity: 0
  }
}


